@font-face {
    font-family : segoeui;
    src         : url(../../fonts/segoeui.ttf);
}

@import "init";
* {
    font-family : $font-family;
}

.mic_wrapper {
    position : relative;
    input { height : 30px; }
    .fa {
        position  : absolute;
        top       : 4px;
        right     : 40px;
        font-size : 20px;
        z-index   : 2;
    }
}

input[readonly], select[readonly] {
    cursor           : pointer !important;
    background-color : #FFF !important;
}

textarea {
    resize : none;
}

.clear {
    clear : both;
}

.pagination > li.paginate_button {
    > a {
        color : #3A3A3C;
    }
    &.active > a {
        color : #FFF;
    }
}

.pdf {
    .modal.in .modal-dialog {
        transform : none;
    }
    .red-b {
        border : solid 1px #D4202B;
    }
    .red {
        background : #D4202B !important;
    }
    .global-title {
        width          : 100%;
        height         : auto;
        padding        : 10px;
        padding-top    : 1px;
        padding-bottom : 4px;
        color          : #FFF;
        font-size      : 15px;
        position       : relative;
    }
    .right {
        float : right;
    }
    .submit {
        margin-left : 10px;
        border      : none;
        padding     : 5px;
    }
    .bttn {
        cursor         : pointer;
        width          : 120px;
        background     : #4D90FE;
        color          : #FFF;
        padding-left   : 10px;
        padding-bottom : 3px;
        padding-top    : 2px;
    }
}

.disabledContent {
    pointer-events : none;
    opacity        : 0.4;
}