#xray-slider {
    .sp-thumbnail-image-container {
        width    : 100px;
        height   : 80px;
        overflow : hidden;
        float    : left;

        @media (max-width : 500px) {
            display : none;
        }
    }

    .sp-thumbnail-image {
        height : 100%;
        width  : 100%;
    }

    .sp-thumbnail-text {
        width            : 170px;
        float            : right;
        padding          : 8px;
        background-color : #F0F0F0;
        -moz-box-sizing  : border-box;
        box-sizing       : border-box;
    }

    .sp-thumbnail-title {
        margin-bottom  : 5px;
        text-transform : uppercase;
        color          : #333;
    }

    .sp-thumbnail-description {
        font-size : 14px;
        color     : #333;
    }

    .sp-thumbnail {
        @media (max-width : 500px) {
            text-align : center;
        }
    }
}