@import "init";

.no-skin {
    .nav-list {
        & > li {
            & > a, &:hover > a {
                @include bgc();
            }

            .submenu > li > a {
                @include bgc('#D8D9D9', '#D4202B');
            }

            &.active:after {
                border-color :  #3A3A3C;
            }
        }

        .open > a {
            &, &:hover, &:focus {
                @include bgc();
            }
        }

        li.active > a:after {
            border-right-color : #3A3A3C;
        }
    }
}
