@import url("http://fonts.googleapis.com/css?family=Lato:100,300,400,700,900,400italic");

.event-list {
    list-style  : none;
    font-family : 'Lato', sans-serif;
    margin      : 0;
    padding     : 0;

    & > li {
        background-color : rgb(255, 255, 255);
        box-shadow       : 0 0 5px rgba(51, 51, 51, 0.7);
        padding          : 0;
        margin           : 0 0 20px;

        & > time {
            display          : inline-block;
            width            : 100%;
            color            : rgb(255, 255, 255);
            background-color : rgb(197, 44, 102);
            padding          : 5px;
            text-align       : center;
            text-transform   : uppercase;

            &.current {
                background-color : #2EBB3B;
            }
            &.wait {
                background-color : #BB860D;
            }
            &.finish {
                background-color : #D4202B;
            }

            & > span {
                display : none;
            }

            & > .day {
                display     : block;
                font-size   : 56pt;
                font-weight : 100;
                line-height : 1;
            }
        }

        time > .month {
            display       : block;
            font-size     : 24pt;
            font-weight   : 900;
            line-height   : 1;
            margin-bottom : 9px;
            margin-top    : 9px;
        }

        & > .info {
            padding-top : 5px;
            text-align  : center;

            & > .title {
                font-size   : 17pt;
                font-weight : 700;
                margin      : 0px;
            }

            & > .desc {
                font-size   : 13pt;
                font-weight : 300;
                margin      : 0px;
            }
        }
    }
}

.event-list > li > .info > ul,
.event-list > li > .social > ul {
    display    : table;
    list-style : none;
    margin     : 10px 0px 0px;
    padding    : 0px;
    width      : 100%;
    text-align : center;
}

.event-list > li > .social > ul {
    margin : 0px;
}

.event-list > li > .info > ul > li,
.event-list > li > .social > ul > li {
    display     : table-cell;
    cursor      : pointer;
    color       : rgb(30, 30, 30);
    font-size   : 11pt;
    font-weight : 300;
    padding     : 3px 0px;
}

.event-list > li > .info > ul > li > a {
    display         : block;
    width           : 100%;
    color           : rgb(30, 30, 30);
    text-decoration : none;
}

.event-list > li > .social > ul > li {
    padding : 0px;
}

.event-list > li > .social > ul > li > a {
    padding : 3px 0px;
}

.event-list > li > .info > ul > li:hover,
.event-list > li > .social > ul > li:hover {
    color            : rgb(30, 30, 30);
    background-color : rgb(200, 200, 200);
}

.facebook a,
.twitter a,
.google-plus a {
    display : block;
    width   : 100%;
    color   : rgb(75, 110, 168) !important;
}

.twitter a {
    color : rgb(79, 213, 248) !important;
}

.google-plus a {
    color : rgb(221, 75, 57) !important;
}

.facebook:hover a {
    color            : rgb(255, 255, 255) !important;
    background-color : rgb(75, 110, 168) !important;
}

.twitter:hover a {
    color            : rgb(255, 255, 255) !important;
    background-color : rgb(79, 213, 248) !important;
}

.google-plus:hover a {
    color            : rgb(255, 255, 255) !important;
    background-color : rgb(221, 75, 57) !important;
}

@media (min-width : 768px) {
    .event-list > li {
        position : relative;
        display  : block;
        width    : 100%;
        height   : 120px;
        padding  : 0px;
    }
    .event-list > li > time,
    .event-list > li > img {
        display : inline-block;
    }
    .event-list > li > time,
    .event-list > li > img {
        width : 120px;
        float : left;
    }
    .event-list > li > .info {
        background-color : rgb(245, 245, 245);
        overflow         : hidden;
    }
    .event-list > li > time,
    .event-list > li > img {
        width   : 120px;
        height  : 120px;
        padding : 0px;
        margin  : 0px;
    }
    .event-list > li > .info {
        position      : relative;
        height        : 120px;
        text-align    : left;
        padding-right : 40px;
    }
    .event-list > li > .info > .title,
    .event-list > li > .info > .desc {
        padding : 0px 10px;
    }
    .event-list > li > .info > ul {
        position : absolute;
        left     : 0px;
        bottom   : 0px;
    }
    .event-list > li > .social {
        position : absolute;
        top      : 0px;
        right    : 0px;
        display  : block;
        width    : 40px;
    }
    .event-list > li > .social > ul {
        border-left : 1px solid rgb(230, 230, 230);
    }
    .event-list > li > .social > ul > li {
        display : block;
        padding : 0px;
    }
    .event-list > li > .social > ul > li > a {
        display : block;
        width   : 40px;
        padding : 10px 0px 9px;
    }
}

.panel-white {
    background : #FFF;
    border     : 1px solid #E9E9E9 !important;
}

.panel {
    -webkit-box-shadow : 0 5px 5px -5px rgba(0, 0, 0, .1);
    -moz-box-shadow    : 0 5px 5px -5px rgba(0, 0, 0, .1);
    box-shadow         : 0 5px 5px -5px rgba(0, 0, 0, .1);
    border             : 0 !important;
    margin-bottom      : 25px;
    border-radius      : 0;
}

.panel .panel-body {
    position                   : relative;
    padding                    : 20px;
    border-bottom-left-radius  : 0;
    border-bottom-right-radius : 0;
}

.info-box .info-box-stats {
    float      : left;
    margin-top : 5px;
}

.info-box .info-box-stats p {
    font-size     : 28px;
    margin-bottom : 14px;
    color         : #374051;
    font-weight   : 400;
}

p {
    display        : block;
    letter-spacing : normal;
    line-height    : 22px;
    margin         : 0 0 10px;
    font-style     : normal;
    white-space    : normal;
}

.f-normal, .f-thin, h1, h2, h3, h4, h5, h6, p {
    font-weight : 400;
}

body, p {
    font-size : 13px;
}

.info-box .info-box-stats span.info-box-title {
    display       : block;
    font-size     : 13px;
    margin-bottom : 10px;
    color         : #B0B0B0;
}

.info-box .info-box-icon {
    float : right;
}

.info-box .info-box-icon i {
    font-size : 31px;
    color     : #B0B0B0;
}

.info-box .info-box-progress {
    clear : both;
}

.info-box .progress {
    margin : 0;
}

.progress-xs {
    height : 5px;
}

.progress-squared {
    border-radius : 0;
}

.progress {
    border-radius : 2px;
}

.bs-n {
    -webkit-box-shadow : none;
    -moz-box-shadow    : none;
    box-shadow         : none;
}

.progress-bar-success {
    background-color : #22BAA0;
}

.progress-bar {
    background-color : #7A6FBE;
}

.progress-bar-success {
    background-color : #5CB85C;
}

.bgcolor_picker {
    table {
        tr {
            display : inline-block;
            padding : 5px;
            td {
                border-top : none;
            }
        }
    }
}