.sante24 {
    .home-banner {
        margin-top     : 32px;
        border-radius  : 0px;
        padding-bottom : 31px;
        border         : 1px solid #9F9F9F;
        border-top     : 10px solid #D4202B;
        border-bottom  : 10px solid #D4202B;

        h5 {
            color         : #3F3F3F;
            padding       : 0 28px;
            font          : 400 24px/28px Open Sans;
            margin-top    : 20px;
            margin-bottom : 19px;
            text-align    : center;
            height        : 80px !important;
            overflow      : hidden;
        }

        p {
            color      : #9F9F9F;
            padding    : 0 20px 0 20px;
            text-align : justify;
        }
        .btn {
            margin-top : 6px;
            padding    : 8px 21px 12px 21px;
            text-align : center;
        }

        img.img {
            width  : 100%;
            height : 200px !important;
        }
        .realimg {
            width : 100%;
        }

    }

    .description {
        border        : none !important;
        margin-bottom : 0 !important;
        padding       : 0 !important;
        height        : 120px;
        overflow      : hidden;
        direction     : rtl;
    }
    .row {
        margin-left  : 0 !important;
        margin-right : 0 !important;
    }

    .sante24 .btn {
        background      : none;
        box-shadow      : none;
        padding         : 20px;
        text-shadow     : none;
        text-decoration : none;
        border-radius   : 0px;
        border          : none;
        font            : 400 15px/19px Open Sans;
        background      : #D4202B;
        color           : #FFF;
        letter-spacing  : 0;
        transition      : all 0.5s;
    }

    .row {
        margin-left  : 0 !important;
        margin-right : 0 !important;
    }
}

.sante24 .btn {
    box-shadow       : none;
    padding          : 20px;
    text-shadow      : none;
    text-decoration  : none;
    border-radius    : 0px;
    border           : none;
    font             : 400 15px/19px Open Sans;
    color            : #FFF;
    letter-spacing   : 0;
    transition       : all 0.5s;
    outline          : none;
    background-color : #D4202B !important;
}

.hespress h1.page_title {
    color       : #000080;
    font-size   : 24px;
    font-weight : bold;
    margin      : 10px 0;
    text-align  : center;
}

.hespress #article_holder .image {
    float      : none;
    text-align : center;
    border     : none;
    margin     : 10px auto;
}

.hespress #article_body {
    font-size     : 13pt;
    line-height   : 150%;
    width         : 100%;
    overflow      : hidden;
    margin-bottom : 12px;
}

.hespress .story_author {
    text-align  : right;
    font-weight : bold;
    color       : #DC143C;
    font-family : "Arabic Transparent";
    font-size   : 14pt;
}

.hespress .story_stamp {
    text-align     : right;
    padding-bottom : 0px;
    margin-bottom  : 0px;
    color          : #777;
}

.hespress .story_date {
    font-size     : 11px;
    padding-right : 0px;
}

.hespress #article_body p {
    direction   : rtl;
    font-size   : 13pt;
    font-weight : bold;
    line-height : 150%;
    margin      : 0 0 10pt;
    padding     : 4px;
    text-align  : justify;
    text-indent : 6px;
}

.salat-container {
    .salat {
        direction        : rtl;
        width            : 100%;
        background-color : #3A3A3C;
        height           : 40px;
        padding-top      : 20px;
        padding-bottom   : 60px;
        border-top       : 5px solid #D4202B;
        border-bottom    : 5px solid #D4202B;
    }

    #horaire {
        font-size : 16px;
        direction : rtl;
        width     : 690px;
    }

    #horaire td {
        border-bottom : 1px solid #EEEADE;
    }

    .cournt {
        background-color : #ECE8DC;
        border-bottom    : 2px solid #69633F !important;
        border-top       : 2px solid #69633F !important;
        color            : #D4202B;
        font-weight      : bold;
    }

    .wakt {
        color         : #D4202B;
        font-weight   : bold;
        font-size     : 18px;
        padding-right : 5px;
    }

    .salatHijri {
        direction    : rtl;
        width        : 100%;
        margin-left  : auto;
        margin-right : auto;
    }

    form[name="horairep"] div {
        color            : #000 !important;
        border-top       : 3px solid #D4202B !important;
        border-bottom    : 3px solid #D4202B !important;
        background-color : #3A3A3C !important;
        padding-top      : 10px !important;
        padding-bottom   : 10px !important;
    }

    table#horaire {
        width       : 100% !important;
        font-size   : 1.2em;
        font-weight : bold;
    }

    select[name="ville"] {
        font-size   : 1.2em;
        font-weight : bold;
    }

    select[name="ville"] + a {
        margin-bottom   : 20px;
        margin-left     : 10px;
        margin-right    : 10px;
        width           : 150px;
        height          : 43px;
        padding         : 7px;
        text-align      : center;
        color           : #000;
        background      : #444;
        background      : -webkit-linear-gradient(#555, #D4202B) !important;
        background      : -moz-linear-gradient(#555, #D4202B) !important;
        background      : -ms-linear-gradient(#555, #D4202B) !important;
        background      : -o-linear-gradient(#555, #D4202B) !important;
        background      : linear-gradient(#555, #D4202B) !important;
        border-radius   : 0px !important;
        text-shadow     : 0px 1px 0px rgba(255, 255, 255, 0.2);
        box-shadow      : 0 0 5px rgba(0, 0, 0, 0.5), 0 -1px 0 rgba(255, 255, 255, 0.4);
        text-decoration : none;
        color           : #FFF;
        font-size       : 1em;
        line-height     : 35px;
        font-weight     : bold;
    }

    .salatHijri form#MyForm {
        display : none;
    }
}

.lemonde {
    .video_player {
        text-align : center;
    }
    .content-article-body .taille_courante {
        font-size   : 1.6rem;
        font-weight : 400;
    }
    .tt2, .tt2_capital {
        font-size : 3.2rem;
        margin    : 0 0 .5rem;
    }
    .tt1, .tt2, .tt3, .tt4, .tt5, .tt6 {
        display     : block;
        font-family : "The Serif Office", Georgia, serif;
        font-weight : normal;
        line-height : 105%;
    }

    .contenu_article {
        font : normal 1.5rem/140% Helvetica, Arial, FreeSans, sans-serif;
    }

    .article h2.taille_courante {
        font-size : inherit;
    }
    .article p, .article h2.taille_courante {
        margin : 15px 0;
    }
    .article h2 {
        font-weight : normal;
    }

    .intertitre {
        margin      : 32px 0 15px;
        font-family : "The Serif Office", Georgia, serif;
        clear       : both;
        padding     : 0 10px;
        color       : #D4202B !important;
    }
    a {
        pointer-events : none;
        color          : #393939;
        cursor         : default;
    }

    [itemprop="Headline"] {
        margin-bottom : 1px !important;
        text-align    : center !important;
        color         : #D4202B !important;
    }

    .illustration_haut, .galerie {
        text-align : center;
        width      : 100% !important;
        max-width  : 100% !important;
        margin     : 30px 0 !important;

        figure {
            margin : 10px 0 !important;
        }

        img {
            width     : 50% !important;
            max-width : 100% !important;
            margin    : 0 auto !important;
        }
    }
    .encart_retrait_gauche {
        @media (min-width : 768px) {
            margin-right : 16px;
            float        : left;
            width        : auto;
            max-width    : 204px;
        }
        .accroche {
            display        : block;
            min-width      : 4rem;
            color          : #2E3942;
            font-family    : Fette, "Arial Narrow", sans-serif;
            letter-spacing : .2rem;
            line-height    : 120%;
            text-transform : uppercase;
            @media (max-width : 999px) {
                font-size : 2rem;
            }
            @media (max-width : 767px) {
                text-align : center;
            }

            &::before, &::after {
                content       : '';
                display       : block;
                height        : 1px;
                min-width     : 4rem;
                background    : #D2D6DB;
                margin-bottom : 1.6rem;
                margin-top    : 1.6rem;
            }
        }
    }
}

.encyclopedieArticle {
    .doc-title {
        margin-bottom : 1px;
        text-align    : center;
    }
    .doc-title h1, .doc-content h2 {
        color : #D4202B !important;
    }

    .col-md-18 {
        position      : relative;
        min-height    : 1px;
        padding-left  : 1px;
        padding-right : 1px;
    }

    .col-md-18 > h1 {
        font    : 300 35px/40px Lato;
        padding : 15px;
        color   : #282828;
    }
    div.row.doc-content > div.col-md-18 h2 {
        clear         : both;
        font          : 400 18px/23px Lato;
        margin-top    : 10px;
        margin-bottom : 15px;
        padding       : 0 10px;
        color         : #282828;
    }
    div.row.doc-content > div.col-md-18 p {
        font          : 400 14px/19px Lato;
        margin-top    : 10px;
        margin-bottom : 20px;
        padding       : 0 10px;
        text-align    : justify;
        color         : #282828;
    }
    div.row.doc-content > div.col-md-18 ul {
        font          : 400 14px/22px Lato;
        text-align    : justify;
        margin-top    : 10px;
        margin-bottom : 20px;
        padding-left  : 10px;
        padding-right : 10px;
        color         : #282828;
        list-style    : none;
    }
    a {
        pointer-events : none;
        cursor         : default;
        color          : #000;
    }
}

.caducee {
    .post-desc {
        .post-title {
            margin-bottom : 10px;
            text-align    : center;
            color         : #D4202B !important;
        }
    }
    a {
        pointer-events : none;
        cursor         : default;
        color          : #000;
    }
}

.vulgarisMedical {
    .node-header h1 {
        margin-bottom : 10px;
        text-align    : center;
        color         : #D4202B !important;
    }
    .field {
        margin-bottom : 15px;
        clear         : both;
    }

    .node .field-name-field-image {
        text-align : center;
    }

    article .node-content .field {
        font-size   : 1.0769em;
        line-height : 1.575em;
    }
    .media_embed {
        text-align : center;
        iframe {
            max-width : 100%;
        }
    }
    .rteindent1 {
        margin-left : 40px;
    }
    p {
        font-size     : 1em;
        margin-bottom : 1.5em;
    }
    .rteright {
        text-align : right;
    }
    .rtecenter {
        text-align : center;
    }
    a {
        pointer-events : none;
        cursor         : default;
        color          : #008000;
    }
}

.journaldemontreal {
    .article-container {
        position : relative;
        padding  : 0;

        .article-section-narrow {
            width         : 100%;
            max-width     : 1000px;
            margin-left   : auto;
            margin-right  : auto;
            padding-left  : 10px;
            padding-right : 10px;
        }

        h1.short, .title-groupe h1 {
            font-size     : 2em;
            margin-bottom : 10px;
            text-align    : center;
            color         : #D4202B !important;
        }
    }

    .hidden-element h2 {
        color : #D4202B !important;
    }

    .title-groupe {
        padding-top : 20px;
    }

    a {
        pointer-events : none;
        cursor         : default;
        color          : #000;
    }

    .article-main-image {
        position    : relative;
        background  : #FFF;
        z-index     : 1;
        width       : 100%;
        margin      : 0;
        padding-top : 8px;
        text-align  : center;

        img {
            width     : 50%;
            max-width : 100%;
            margin    : 10px auto 20px;
        }
    }
}