#eventModal, #dossierInfoModal, #AjouterPatientModal {
    input[disabled], fieldset[disabled] .form-control {
        cursor           : default;
        background-color : #FFF !important;
    }
    .modal-content {
        border-radius : 0px;
        border        : solid 1px #D4202B;
        .modal-header {
            padding    : 0 !important;
            background : #D4202B !important;
            .global-title {
                padding   : 10px 30px;
                width     : 100%;
                height    : auto;
                color     : #FFF;
                font-size : 15px;
            }
        }
        .modal-body {
            padding : 0 !important;

            button.afficher, button.consulter {
                display          : block;
                background-color : #4D90FE !important;
                color            : #FFF;
                border-radius    : 0px;
                width            : 100%;
                text-align       : center;
            }
            .global-content {
                width   : 100%;
                height  : auto;
                padding : 30px 0 0;
                .input-group, .field {
                    width           : 100% !important;
                    position        : relative;
                    display         : table;
                    border-collapse : separate;
                    margin-bottom   : 5px;
                    label.input-group-addon {
                        height        : 30px !important;
                        width         : 24% !important;
                        border-right  : 0;
                        border-radius : 0 !important;
                        text-align    : left !important;
                    }
                    textarea {
                        resize : none;
                    }
                }
                span.help-block {
                    color : #A94442;
                }
                .has-feedback label ~ .form-control-feedback {
                    top : 0px !important;
                }
                fieldset.scheduler-border {
                    border             : 1px groove #DDD !important;
                    padding            : 12px !important;
                    margin             : 30px 12px !important;
                    -webkit-box-shadow : 0px 0px 0px 0px #000;
                    box-shadow         : 0px 0px 0px 0px #000;

                    legend.scheduler-border {
                        font-size     : 1.2em !important;
                        font-weight   : bold !important;
                        text-align    : left !important;
                        width         : auto;
                        padding       : 0 10px;
                        border-bottom : none;
                    }
                }
            }
        }
        .modal-footer {
            padding    : 10px 30px;
            margin-top : 15px;
            text-align : right;
            border-top : 1px solid #E5E5E5;

            button {
                cursor      : pointer;
                width       : 120px;
                color       : #FFF;
                margin-left : 10px;
                border      : none;
                padding     : 5px;

                &.blue {
                    background : #4D90FE !important;
                    color      : #FFF !important;
                }

                &.red {
                    background : #D4202B !important;
                    color      : #FFF !important;
                }
                &.green {
                    background : #35AA47 !important;
                    color      : #FFF !important;
                }
            }
        }
    }
}

#AjouterPatientModal {
    .form-horizontal .has-feedback .form-control-feedback {
        right : 6px;
    }
    .has-feedback label ~ .form-control-feedback {
        top : 0px;
    }
}

#dossierInfoModal, #AjouterPatientModal {
    .input-group {
        width : 100%;
        input, select {
            height : 28px;
        }
    }
    label.input-group-addon {
        width : 140px !important;
    }
    .field {
        height : 50px;
    }

    .modal-dialog {
        width  : 75%;
        margin : 10px auto;
        @media screen and (max-width : 1080px) {
            width : 99%;
        }
    }

    button.btn.consulter a, button.btn.afficher a {
        color           : #FFF;
        text-decoration : none;
    }
}

#dossierInfoModal {
    .mic_wrapper .fa.fa-microphone-slash {
        display : none;
    }
}

.fc-state-default, .fc-state-default .fc-button-inner {
    border           : none;
    background-color : #D4202B;
    color            : #FFF;
    background-image : none;
    box-shadow       : none;
    text-shadow      : none;
    border-radius    : 0 !important;
    margin-left      : 2px;
}

.fc .fc-button-group > * {
    margin : 0 1px 0 0;
}