@import "init";

.global {
    outline : 2px solid #D4202B;

    .global-title {
        padding : 5px;
        @include bgc();
    }

    .global-body {
        padding : 10px;

        .field {
            //margin : 10px 0px;
            height : $field-height;
        }

        .input-group {
            width : 100%;

            .input-group-addon {
                width : $input-addon-width !important;
            }

            input, select {
                height : $input-height;
            }

            .iradio + label {
                font-weight : bold;
                margin-left : 10px;
            }

            .form-control-feedback {
                top   : -2px !important;
                right : 0px !important;
            }

            &.has-error.has-feedback + .help-block {
                color    : $feedColor;
                bottom   : 3px !important;
                position : relative;
                display  : block;
            }

        }

        .btn-reset {
            background-color : #D4202B !important;
        }
        .btn-submit {
            background-color : #4D90FE !important;
        }
    }

    .clear {
        clear : both;
    }
}

.btn-dossier {
    cursor         : pointer;
    width          : 100%;
    background     : #666 !important;
    color          : #FFF;
    padding-left   : 10px;
    padding-bottom : 3px;
    padding-top    : 2px;
}