@import "init";

fieldset.scheduler-border {
    border             : 1px groove #DDD !important;
    padding            : 12px !important;
    margin             : 30px 12px !important;
    -webkit-box-shadow : 0px 0px 0px 0px #000;
    box-shadow         : 0px 0px 0px 0px #000;

    legend.scheduler-border {
        font-size     : 1.2em !important;
        font-weight   : bold !important;
        text-align    : left !important;
        width         : auto;
        padding       : 0 10px;
        border-bottom : none;
    }
}

.block {
    display        : inline-block;
    white-space    : normal;
    vertical-align : middle;
    color          : #FFF;
    @include vertical-align();
}

.plupload_logo, .plupload_header_title, .plupload_header_text {
    display : none !important;
}

.plupload_wrapper {
    min-width : 100% !important;
}

.action {
    outline  : 3px solid #FFF;
    height   : 140px;
    position : relative;
    cursor   : pointer;

    .icone {
        @extend .block;
        font-weight : bold;
    }

    .text {
        @extend .block;
        font-weight : bold;
    }

    .add-bottom {
        width          : 100%;
        height         : $add-bottom-height;
        position       : absolute;
        font-size      : 15px;
        color          : #FFF;
        bottom         : 0;
        right          : 0;
        padding-top    : 4px;
        padding-bottom : 6px;

        .add-l {
            padding-left : 10px;
            float        : left;
        }

        .add-r {
            padding-right : 10px;
            float         : right;
            font-size     : 20px;
        }
    }

}

.dropzone {
    border-radius : 0;
    border        : 1px solid rgba(0, 0, 0, 0.06);
}

.dropzone .dz-default.dz-message {
    background-image : none;
    font-size        : 24px;
    text-align       : center;
    line-height      : 32px;
    left             : 0;
    width            : 100%;
    margin-left      : auto;
}

.dropzone .dz-default.dz-message span {
    display : inline;
    color   : #555;
}

.dropzone .dz-default.dz-message span .upload-icon {
    opacity    : 0.7;
    filter     : alpha(opacity=70);
    margin-top : 8px;
    cursor     : pointer;
}

.dropzone .dz-default.dz-message span .upload-icon:hover {
    opacity : 1;
    filter  : alpha(opacity=100);
}

.dropzone .dz-preview .dz-error-mark,
.dropzone-previews .dz-preview .dz-error-mark,
.dropzone .dz-preview .dz-success-mark,
.dropzone-previews .dz-preview .dz-success-mark {
    background-image : none;
    background-color : rgba(255, 255, 255, 0.8);
    border-radius    : 100%;
    text-align       : center;
    line-height      : 35px;
}

.dropzone .dz-preview .dz-error-mark:before,
.dropzone-previews .dz-preview .dz-error-mark:before {
    font-family : FontAwesome;
    font-size   : 30px;
    color       : #DB6262;
    content     : "\f00d";
}

.dropzone .dz-preview .dz-success-mark:before,
.dropzone-previews .dz-preview .dz-success-mark:before {
    font-family : FontAwesome;
    font-size   : 30px;
    color       : #6DA552;
    content     : "\f00c";
}

.dropzone a.dz-remove,
.dropzone-previews a.dz-remove {
    border        : none;
    border-radius : 0;
    color         : #FFF;
    background    : #D15B47;
    cursor        : pointer;
}

.dropzone a.dz-remove:hover,
.dropzone-previews a.dz-remove:hover {
    color      : #FFF;
    background : #B74635;
}

.dropzone .progress,
.dropzone-previews .progress {
    margin-bottom : 0;
}

.dropzone .dz-preview.dz-success .progress,
.dropzone-previews .dz-preview.dz-success .progress,
.dropzone .dz-preview.dz-error .progress,
.dropzone-previews .dz-preview.dz-error .progress {
    display : none;
}

.loader img {
    width    : 20px;
    position : absolute;
    right    : 4px;
    top      : 4px;
    z-index  : 2;
    display  : none;
}